import React from "react";
import Table from "react-bootstrap/Table";
import CustomSelect from "../Select/customSelect";
import "./staticTable.css"; // Ensure updated styles are applied

const StaticTable = ({
  columnHeaders,
  handleSelectChange,
  dropdownOptions = [],
}) => {
  return (
    <Table bordered className="table-light">
      <thead>
        <tr>
          <th className="header-styling">Column Name</th>
          <th className="header-styling">Attribute Name</th>
        </tr>
      </thead>
      <tbody>
        {columnHeaders.map((column, index) => {
          const {
            header,
            currentMapping,
            defaultLabel,
            disabled,
            hasNLS,
            isMandatory,
          } = column;
          // Get filtered dropdown options for this specific column
          const filteredOptions = dropdownOptions(header);

          // Modified logic to handle selected values
          let selectedValue;
          if (disabled) {
            // For disabled fields, don't worry about dropdown validation
            // We'll handle this with CSS and the disabled attribute
            selectedValue = header;
          } else if (hasNLS && !currentMapping) {
            // For columns with NLS match but no mapping yet
            // Use the header as display value but mark dropdown as empty to show placeholder
            selectedValue = "";
          } else {
            // For normal columns, use mapping if exists or empty
            selectedValue = currentMapping?.uiLabel || "";
          }

          return (
            <tr key={index} className={disabled ? "disabled-row" : ""}>
              <td className={disabled ? "disabled-cell" : ""}>{header}</td>
              <td
                className={`dropdown-cell ${
                  disabled ? "disabled-dropdown" : ""
                }`}
              >
                <div className="dropdown-wrapper">
                  <CustomSelect
                    index={index}
                    selectedValue={selectedValue}
                    onChange={(index, value) =>
                      handleSelectChange(header, value)
                    }
                    size="lg"
                    className={`w-100 ${
                      disabled ? "custom-select-disabled" : ""
                    }`}
                    disabled={disabled}
                    options={{
                      defaultLabel: defaultLabel,
                      list: filteredOptions,
                    }}
                  />
                  {disabled && <div className="disabled-overlay">{header}</div>}
                </div>
              </td>
            </tr>
          );
        })}
      </tbody>
    </Table>
  );
};

export default StaticTable;
