import React, { useEffect } from "react";
import { Form } from "react-bootstrap";
import "./customSelect.css";

const CustomSelect = ({
  index,
  selectedValue,
  onChange,
  size,
  className,
  disabled,
  options,
}) => {
  const defaultLabel = options?.defaultLabel || "Please select";
  const list = options?.list || [];

  // Debug output to check what's being received
  useEffect(() => {
    console.log(`CustomSelect for index ${index}:`, {
      selectedValue,
      list,
      hasGroupedOptions:
        Array.isArray(list) &&
        list.length > 0 &&
        list[0] &&
        "label" in list[0] &&
        "options" in list[0],
    });
  }, [selectedValue, list, index]);

  const handleChange = (e) => {
    // If index is provided use it, otherwise just pass the value
    if (index !== undefined) {
      onChange(index, e.target.value);
    } else {
      onChange(e.target.value);
    }
  };
  // Check if we have grouped options (array of objects with label and options properties)
  const hasGroupedOptions =
    Array.isArray(list) &&
    list.length > 0 &&
    list[0] &&
    "label" in list[0] &&
    "options" in list[0];

  // Validate selected value exists in options
  const valueExists = hasGroupedOptions
    ? list.some(
        (group) =>
          Array.isArray(group.options) &&
          group.options.some((item) => item.value === selectedValue)
      )
    : list.some((item) => item.value === selectedValue);

  // If selected value doesn't exist in options, use empty string to show placeholder
  const validatedValue = valueExists ? selectedValue : "";

  return (
    <Form.Select
      aria-label="Attribute selection"
      value={validatedValue}
      onChange={handleChange}
      size={size}
      className={className}
      disabled={disabled}
    >
      <option value="">{defaultLabel}</option> {/* Default option */}
      {hasGroupedOptions
        ? // Render grouped options with optgroup
          list.map((group, groupIndex) => (
            <optgroup key={`group-${groupIndex}`} label={group.label}>
              {Array.isArray(group.options) &&
                group.options.map((item, optionIndex) => (
                  <option
                    key={`option-${groupIndex}-${optionIndex}`}
                    value={item.value}
                  >
                    {item.label}
                  </option>
                ))}
            </optgroup>
          ))
        : // Render flat list of options
          list.map((item, idx) => (
            <option key={`option-${idx}`} value={item.value}>
              {item.label}
            </option>
          ))}
    </Form.Select>
  );
};

export default CustomSelect;
